.individualPhoto {
  margin: 15px;
}

img {
  max-height: 85vh;
  border: 8px #dadada outset;
  -webkit-transition: 700ms;
  -o-transition: 700ms;
  transition: 700ms;
}

img:hover {
  -webkit-transform: translate(-4px, -4px);
      -ms-transform: translate(-4px, -4px);
          transform: translate(-4px, -4px);
  -webkit-box-shadow: 6px 6px 9px 0px black;
          box-shadow: 6px 6px 9px 0px black;
}


